import { Fragment, useEffect, useState } from 'react'
import { Download, MoreVertical, User } from 'react-feather'
import { Col, DropdownItem, DropdownMenu, DropdownToggle, Row, UncontrolledDropdown } from 'reactstrap'
import { gapi } from "gapi-script"
import moment from 'moment'
import { Link } from 'react-router-dom'
const xlsimage = require('@src/assets/images/icons/xls.png').default
const docimage = require('@src/assets/images/icons/docs.png').default
const pdfimage = require('@src/assets/images/icons/pdf.png').default
const pngimage = require('@src/assets/images/icons/png.png').default
const pptimage = require('@src/assets/images/icons/ppt.png').default
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

const Files = ({ file, listViewFlg }) => {

    const [block, setBlock] = useState(false)
    const Loader = () => {
        return (
            <Fragment>
                <Spinner/>
            </Fragment>
        )
    }

    const imageOption = [
        { type: ['xlsx', 'xls'], Image: xlsimage },
        { type: ['docs'], Image: docimage },
        { type: ['pdf'], Image: pdfimage },
        { type: ['png', 'jpg', 'jpeg', 'gif'], Image: pngimage },
        { type: ['ppt'], Image: pptimage }
    ]

    const srcPath = (file) => {
        const temp = imageOption.find((oj) => oj?.type?.includes(file?.extension))
        return temp?.Image ?? ''
    }

    const SCOPE = 'https://www.googleapis.com/auth/drive https://www.googleapis.com/auth/drive.readonly https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/gmail.send'

    const [fileData, setFileData] = useState(0)

    useEffect(() => {
        function start() {
            setBlock(true)
            gapi.client.init({
                apiKey: process.env.REACT_APP_GOOGLE_API_KEY,
                clientId: process.env.REACT_APP_GOOGLE_CLIENT_ID,
                scope: SCOPE
            })

            gapi.client.load("drive", "v3").then(() => {
                try {
                    gapi.client.drive.files
                        .get({ fileId: file?.driveid, fields: "modifiedTime, size, webViewLink, webContentLink, thumbnailLink" })
                        .then((response) => {
                            const fileContent = response.result
                            setFileData(fileContent)
                            setBlock(false)
                        })
                        .catch((error) => {
                            console.log(error)
                            setBlock(false)
                        })
                } catch (error) {
                    console.log(error)
                    setBlock(false)
                }
            })
        }

        gapi.load("client:auth2", start)
    }, [])

    return (
        <UILoader blocking={block} loader={<Loader />}>
            <div className={`border rounded bg-light-secondary cursor-pointer border-1 file ${!listViewFlg ? 'me-50 width-15-per mb-25 ' : 'm-50'}`} >
                <div className="card-img-top d-flex pb-0 pt-1 px-1">

                    <a className={`${!listViewFlg ? 'w-100' : 'width-50-per '}`} href={fileData?.webViewLink} target={'_blank'}>
                        <div className={`d-flex align-items-center justify-content-center ${!listViewFlg ? 'w-100' : 'width-50-per '}`}>
                            <img src={srcPath(file)} alt="file-icon" height="20" /> <p className="card-text file-name mb-0 ms-75 pe-75">{file?.name}</p>
                        </div>
                    </a>
                    {listViewFlg &&
                        <Fragment>
                            <div div className='width-15-per text-truncate'>
                                {file?.username}
                            </div>
                            <div className='width-10-per text-center'>
                                {fileData?.size ? parseFloat(fileData?.size / 1024).toFixed(3) : 0} KB
                            </div>
                            <div className='width-15-per text-center'>
                                {moment(fileData?.modifiedTime).format('MMM DD, Y')}
                            </div>
                        </Fragment>
                    }

                    <div className="dropdown float-right width-10-per text-end">
                        <UncontrolledDropdown>
                            <DropdownToggle tag='div' className='btn btn-sm'>
                                <MoreVertical size={14} className='cursor-pointer' />
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem>
                                    <a target='_blank' to={fileData?.webContentLink} download={file?.name}>
                                        <Download size={14} /> Download
                                    </a>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    </div>
                </div>
                {!listViewFlg &&
                    <Col className='p-1 height-150'>
                        <a href={fileData?.webViewLink} target={'_blank'}>
                            <img referrerPolicy="no-referrer" src={fileData?.thumbnailLink} style={{ width: '100%', height: '100%' }} />
                        </a>
                    </Col>

                }
            </div>
        </UILoader>
    )
}

export default Files