
import Files from './Files'
import FolderList from './Folder'

import PerfectScrollbar from 'react-perfect-scrollbar'
import { ArrowDownCircle, Grid, List, Search } from 'react-feather'
import { Fragment, useEffect, useState } from 'react'
import { Button, Breadcrumb, BreadcrumbItem, Input } from 'reactstrap'
import { Link, useParams } from 'react-router-dom'
import axios from '@src/configs/axios/axiosConfig'

const FileContainer = () => {

    const { pid } = useParams()

    const [listViewFlg, setListViewFlg] = useState(true)
    const [dataList, setDataList] = useState([])
    const [queryVal, setQueryVal] = useState('')
    const [breadCrumbs, setBreadCrumbs] = useState([])

    const getList = (query = '') => {
        axios.post('/files/list', { parentId: pid, q: query })
            .then((res) => {
                setDataList(res.data.files)
                setBreadCrumbs(res.data.breadcrumbs.reverse())
            })
    }

    useEffect(() => {
        getList('')
        setQueryVal('')
    }, [pid])

    const handleSearch = (value) => {
        getList(value)
        setQueryVal(value)
    }

    return (
        <div className="content-right col-12" style={{ width: '100%' }}>
            <div className="content-wrapper">
                <div className="px-2 row py-50">
                    <Fragment>
                        <Breadcrumb>
                            {breadCrumbs.map((breadcrumb, k) => {
                                return (
                                    <BreadcrumbItem key={k}>
                                        <Link to={`/filemanager/${breadcrumb.DirectoryKey}`}> {breadcrumb.Name} </Link>
                                    </BreadcrumbItem>
                                )
                            })
                            }
                        </Breadcrumb>
                    </Fragment>
                </div>
                <div className="content-body">

                    <div className="body-content-overlay"></div>
                    <div className="file-manager-main-content">

                        <div className="file-manager-content-header d-flex justify-content-between align-items-center">
                            <div className="d-flex align-items-center">
                                <div className="sidebar-toggle d-block d-xl-none float-left align-middle ml-1">
                                    <i data-feather="menu" className="font-medium-5"></i>
                                </div>
                                <div className="input-group input-group-merge shadow-none m-0 flex-grow-1">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text border-0">
                                            <Search size={14} />
                                        </span>
                                    </div>
                                    <Input type="text" className="form-control files-filter border-0 bg-transparent" placeholder="Search" value={queryVal} onChange={(e) => { handleSearch(e.target.value) }} />
                                </div>
                            </div>
                            <div className="d-flex align-items-center">
                                {/* <div className="file-actions show">
                                    <ArrowDownCircle size={16} className='me-1' />
                                </div> */}
                                <div className="btn-group btn-group-toggle view-toggle ml-50" data-toggle="buttons">
                                    <Button size='sm' className={`p-50 ${!listViewFlg ? 'active' : ''}`} outline color='primary' onClick={() => setListViewFlg(false)} >
                                        <Grid size={14} />
                                    </Button>
                                    <Button className={`p-50 ${listViewFlg ? 'active' : ''}`} size='sm' outline color='primary' onClick={() => setListViewFlg(true)} >
                                        <List size={14} />
                                    </Button>
                                </div>
                            </div>
                        </div>
                        <PerfectScrollbar className={`file-manager-content-body p-2`} options={{ wheelPropagation: false }}>
                            <div >
                                <div className={`view-container ${listViewFlg ? 'list-view' : ''} `}>
                                    <div className="files-header mx-2">
                                        <h6 className="font-weight-bold mb-0 width-50-per">Filename</h6>
                                        <h6 className="font-weight-bold mb-0 width-15-per ">Owner</h6>
                                        <h6 className="font-weight-bold mb-0 width-10-per text-center">Size</h6>
                                        <h6 className="font-weight-bold mb-0 width-15-per text-center">Last Modified</h6>
                                        <h6 className="font-weight-bold mb-0 width-10-per"></h6>
                                    </div>
                                    {
                                        dataList.map((item, k) => {
                                            return (

                                                item.type === '2' ? <FolderList key={k} folder={item} listViewFlg={listViewFlg} /> : <Files key={k} file={item} listViewFlg={listViewFlg} />

                                            )
                                        })
                                    }
                                    {dataList.length === 0 &&
                                        <div className="d-none flex-grow-1 align-items-center no-result mb-3">
                                            <i data-feather="alert-circle" className="mr-50"></i>
                                            No Results
                                        </div>
                                    }
                                </div>
                            </div>
                        </PerfectScrollbar>
                    </div>

                    <div className="modal modal-slide-in fade show" id="app-file-manager-info-sidebar">
                        <div className="modal-dialog sidebar-lg">
                            <div className="modal-content p-0">
                                <div className="modal-header d-flex align-items-center justify-content-between mb-1 p-2">
                                    <h5 className="modal-title">menu.js</h5>
                                    <div>
                                        <i data-feather="trash" className="cursor-pointer mr-50" data-dismiss="modal"></i>
                                        <i data-feather="x" className="cursor-pointer" data-dismiss="modal"></i>
                                    </div>
                                </div>
                                <div className="modal-body flex-grow-1 pb-sm-0 pb-1">
                                    <ul className="nav nav-tabs tabs-line" role="tablist">
                                        <li className="nav-item">
                                            <a className="nav-link active" data-toggle="tab" href="#details-tab" role="tab" aria-controls="details-tab" aria-selected="true">
                                                <i data-feather="file"></i>
                                                <span className="align-middle ml-25">Details</span>
                                            </a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link" data-toggle="tab" href="#activity-tab" role="tab" aria-controls="activity-tab" aria-selected="true">
                                                <i data-feather="activity"></i>
                                                <span className="align-middle ml-25">Activity</span>
                                            </a>
                                        </li>
                                    </ul>
                                    <div className="tab-content" id="myTabContent">
                                        <div className="tab-pane fade show active" id="details-tab" role="tabpanel" aria-labelledby="details-tab">
                                            <div className="d-flex flex-column justify-content-center align-items-center py-5">
                                                <img src="../../../app-assets/images/icons/js.png" alt="file-icon" height="64" />
                                                <p className="mb-0 mt-1">54kb</p>
                                            </div>
                                            <h6 className="file-manager-title my-2">Settings</h6>
                                            <ul className="list-unstyled">
                                                <li className="d-flex justify-content-between align-items-center mb-1">
                                                    <span>File Sharing</span>
                                                    <div className="custom-control custom-switch">
                                                        <input type="checkbox" className="custom-control-input" id="sharing" />
                                                        <label className="custom-control-label" htmlFor="sharing"></label>
                                                    </div>
                                                </li>
                                                <li className="d-flex justify-content-between align-items-center mb-1">
                                                    <span>Synchronization</span>
                                                    <div className="custom-control custom-switch">
                                                        <input type="checkbox" className="custom-control-input" checked id="sync" />
                                                        <label className="custom-control-label" htmlFor="sync"></label>
                                                    </div>
                                                </li>
                                                <li className="d-flex justify-content-between align-items-center mb-1">
                                                    <span>Backup</span>
                                                    <div className="custom-control custom-switch">
                                                        <input type="checkbox" className="custom-control-input" id="backup" />
                                                        <label className="custom-control-label" htmlFor="backup"></label>
                                                    </div>
                                                </li>
                                            </ul>
                                            <hr className="my-2" />
                                            <h6 className="file-manager-title my-2">Info</h6>
                                            <ul className="list-unstyled">
                                                <li className="d-flex justify-content-between align-items-center">
                                                    <p>Type</p>
                                                    <p className="font-weight-bold">JS</p>
                                                </li>
                                                <li className="d-flex justify-content-between align-items-center">
                                                    <p>Size</p>
                                                    <p className="font-weight-bold">54kb</p>
                                                </li>
                                                <li className="d-flex justify-content-between align-items-center">
                                                    <p>Location</p>
                                                    <p className="font-weight-bold">Files Documents</p>
                                                </li>
                                                <li className="d-flex justify-content-between align-items-center">
                                                    <p>Owner</p>
                                                    <p className="font-weight-bold">Sheldon Cooper</p>
                                                </li>
                                                <li className="d-flex justify-content-between align-items-center">
                                                    <p>Modified</p>
                                                    <p className="font-weight-bold">12th Aug, 2020</p>
                                                </li>

                                                <li className="d-flex justify-content-between align-items-center">
                                                    <p>Created</p>
                                                    <p className="font-weight-bold">01 Oct, 2019</p>
                                                </li>
                                            </ul>
                                        </div>
                                        <div className="tab-pane fade" id="activity-tab" role="tabpanel" aria-labelledby="activity-tab">
                                            <h6 className="file-manager-title my-2">Today</h6>
                                            <div className="media align-items-center mb-2">
                                                <div className="avatar avatar-sm mr-50">
                                                    <img src="../../../app-assets/images/avatars/5-small.png" alt="avatar" width="28" />
                                                </div>
                                                <div className="media-body">
                                                    <p className="mb-0">
                                                        <span className="font-weight-bold">Mae</span>
                                                        shared the file with
                                                        <span className="font-weight-bold">Howard</span>
                                                    </p>
                                                </div>
                                            </div>
                                            <div className="media align-items-center">
                                                <div className="avatar avatar-sm bg-light-primary mr-50">
                                                    <span className="avatar-content">SC</span>
                                                </div>
                                                <div className="media-body">
                                                    <p className="mb-0">
                                                        <span className="font-weight-bold">Sheldon</span>
                                                        updated the file
                                                    </p>
                                                </div>
                                            </div>
                                            <h6 className="file-manager-title mt-3 mb-2">Yesterday</h6>
                                            <div className="media align-items-center mb-2">
                                                <div className="avatar avatar-sm bg-light-success mr-50">
                                                    <span className="avatar-content">LH</span>
                                                </div>
                                                <div className="media-body">
                                                    <p className="mb-0">
                                                        <span className="font-weight-bold">Leonard</span>
                                                        renamed this file to
                                                        <span className="font-weight-bold">menu.js</span>
                                                    </p>
                                                </div>
                                            </div>
                                            <div className="media align-items-center">
                                                <div className="avatar avatar-sm mr-50">
                                                    <img src="../../../app-assets/images/portrait/small/avatar-s-1.jpg" alt="Avatar" width="28" />
                                                </div>
                                                <div className="media-body">
                                                    <p className="mb-0">
                                                        <span className="font-weight-bold">You</span>
                                                        shared this file with Leonard
                                                    </p>
                                                </div>
                                            </div>
                                            <h6 className="file-manager-title mt-3 mb-2">3 days ago</h6>
                                            <div className="media">
                                                <div className="avatar avatar-sm mr-50">
                                                    <img src="../../../app-assets/images/portrait/small/avatar-s-1.jpg" alt="Avatar" width="28" />
                                                </div>
                                                <div className="media-body">
                                                    <p className="mb-50">
                                                        <span className="font-weight-bold">You</span>
                                                        uploaded this file
                                                    </p>
                                                    <img src="../../../app-assets/images/icons/js.png" alt="Avatar" className="mr-50" height="24" />
                                                    <span className="font-weight-bold">app.js</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="dropdown-menu dropdown-menu-right file-dropdown">
                        <a className="dropdown-item" href="javascript:void(0);">
                            <i data-feather="eye" className="align-middle mr-50"></i>
                            <span className="align-middle">Preview</span>
                        </a>
                        <a className="dropdown-item" href="javascript:void(0);">
                            <i data-feather="user-plus" className="align-middle mr-50"></i>
                            <span className="align-middle">Share</span>
                        </a>
                        <a className="dropdown-item" href="javascript:void(0);">
                            <i data-feather="copy" className="align-middle mr-50"></i>
                            <span className="align-middle">Make a copy</span>
                        </a>
                        <div className="dropdown-divider"></div>
                        <a className="dropdown-item" href="javascript:void(0);">
                            <i data-feather="edit" className="align-middle mr-50"></i>
                            <span className="align-middle">Rename</span>
                        </a>
                        <a className="dropdown-item" href="javascript:void(0);" data-toggle="modal" data-target="#app-file-manager-info-sidebar">
                            <i data-feather="info" className="align-middle mr-50"></i>
                            <span className="align-middle">Info</span>
                        </a>
                        <div className="dropdown-divider"></div>
                        <a className="dropdown-item" href="javascript:void(0);">
                            <i data-feather="trash" className="align-middle mr-50"></i>
                            <span className="align-middle">Delete</span>
                        </a>
                        <a className="dropdown-item" href="javascript:void(0);">
                            <i data-feather="alert-circle" className="align-middle mr-50"></i>
                            <span className="align-middle">Report</span>
                        </a>
                    </div>

                    <div className="modal fade" id="new-folder-modal">
                        <div className="modal-dialog modal-dialog-centered">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title">New Folder</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                    </button>
                                </div>
                                <div className="modal-body">
                                    <input type="text" className="form-control" value="New folder" placeholder="Untitled folder" />
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-primary mr-1" data-dismiss="modal">Create</button>
                                    <button type="button" className="btn btn-outline-secondary" data-dismiss="modal">Cancel</button>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default FileContainer