import { Col } from "reactstrap"
import { Folder, MoreVertical } from 'react-feather'
import { useNavigate } from "react-router-dom"
import { Fragment } from "react"

const FolderList = ({ folder, listViewFlg }) => {

    const navigate = useNavigate()


    return (
        <Col className={`border rounded border-1 folder bg-light-secondary cursor-pointer text-body ${!listViewFlg ? 'col-6 col-lg-2 col-md-2 border-white mb-25 me-50' : 'm-50'}`} onDoubleClick={() => { navigate(`/filemanager/${folder?.id}`) }}>
            <div className="card-img-top d-flex p-1">
                <div className={`d-flex align-items-center justify-content-center ${!listViewFlg ? 'w-100' : 'width-50-per '}`}>
                    <Folder size={16} /> <p className="card-text file-name mb-0 ms-75 pe-75">{folder?.name}</p>
                </div>
                {listViewFlg &&
                    <Fragment>
                        <div className='width-15-per text-truncate'>
                            {folder?.username}
                        </div>
                        <div className='width-10-per text-center'>
                            -
                        </div>
                        <div className='width-15-per text-center'>
                            -
                        </div>
                    </Fragment>
                }
                <div className="dropdown float-right width-10-per text-end">
                    <MoreVertical size={16} className='me-1' />
                </div>
            </div>
        </Col >
    )
}

export default FolderList